import React from 'react'
import '../../assets/css/pages/admin.css'
import {Table} from 'react-bootstrap';

const Usertable = ({users}) => {

     return (
      <div>
        <Table responsive striped bordered hover>
          <thead className="table-head">
            <tr>
              <th>Name</th>
              <th>Mobile</th>
              <th>Whatsapp</th>
              <th>Email</th>
              <th>Company</th>
            </tr>
          </thead>
          <tbody>
            {users.map((user) => (
              <tr>
                <td>{user.name}</td>
                <td>{user.mobile}</td>
                <td>{user.whatsapp}</td>
                <td>{user.email}</td>
                <td>{user.company}</td>
              </tr>
            ))}
          </tbody>
        </Table>
      </div>
    );
}

export default Usertable;
