import React from "react";
import { Link } from "react-router-dom";
import { Container, Row, Col, Card } from "react-bootstrap";
import "../../assets/css/pages/home.css";
import { CaHome } from "../../assets/img/paths";
import Footer from "../../components/general/footer";
import Section1 from "../../components/general/Section1";
import Section2 from "../../components/general/Section2";
import Section3 from "../../components/general/Section3";

const HomePage = () => {
  return (
    <div>
      <Container fluid>
        <Row>
          <Col md={7} className="mt-5 image-section">
            <div className="img-fluid p-5 ml-5">
              <CaHome />
            </div>
          </Col>

          <Col className="m-auto text-section" align="center">
            <Card>
              <Card.Body className="card-items">
                <h1 className="">
                  Make Fun <br /> Creating
                </h1>
                <h6 className="mt-5">Make awesome Formal card!</h6>
                <Link to="/dashboard" className="btn btn-primary mt-5">
                  Get started
                </Link>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
      <Section1 />
      <Section2 />
      <Section3 />
      <Footer />
    </div>
  );
};

export default HomePage;
